.notfound {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #17002f;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.page__return {
    width: 231px;
    height: 66px;
    border-radius: 11px;
    box-shadow: inset 0 -9px 0 0 rgba(0, 0, 0, 0.18);
    background: #73d677;
    font-weight: 700;
    font-size: 23px;
    text-align: center;
}
